import { useEffect, useState } from 'react';
import toast from 'react-hot-toast';
import { AreaChart, Area, XAxis, YAxis, CartesianGrid, Tooltip, ResponsiveContainer, Label } from 'recharts';

//component
import ButtonLoader from '../component/button-loader';

//assets
import * as S from '../assets/css/index';
import BG00 from '../assets/img/BG00.png';
import Logo from '../assets/img/logo.png';

// mui
import TextField from '@mui/material/TextField';
import Autocomplete from '@mui/material/Autocomplete';
import { Button } from '@mui/material';

//api request
import { GetAllCountries, GetAllMinerals, GetInformations } from '../api-requests/index';

function Index() {
	const [countriesList, setCountriesList] = useState([]);
	const [mineralsList, setMineralsList] = useState([]);
	const [selectedCountry, setSelectedCountry] = useState('');
	const [selectedMineral, setSelectedMineral] = useState('');
	const [loader, setLoader] = useState(false);
	const [result, setResult] = useState([]);
	const [selectedinfo, setSelectedInfo] = useState({
		country: null,
		mineral: null,
	});

	useEffect(() => {
		GetAllCountries().then(res => setCountriesList([...new Set(res)]));
		GetAllMinerals().then(res => setMineralsList([...new Set(res)]));
	}, []);

	const capitalizeFirstLetter = string => {
		return string.charAt(0).toUpperCase() + string.slice(1);
	};

	const searchHandler = () => {
		if (!loader) {
			if (selectedCountry === '') {
				toast.error('Please select country first !');
			} else if (selectedMineral === '') {
				toast.error('Please select mineral first !');
			} else if (countriesList.findIndex(item => item === capitalizeFirstLetter(selectedCountry)) === -1) {
				toast.error('This country is not in the list !');
			} else if (mineralsList.findIndex(item => item === capitalizeFirstLetter(selectedMineral)) === -1) {
				toast.error('This mineral is not in the list !');
			} else {
				setLoader(true);
				GetInformations(capitalizeFirstLetter(selectedCountry), capitalizeFirstLetter(selectedMineral)).then(res => {
					setLoader(false);
					setResult(res);
					setSelectedInfo({
						country: selectedCountry,
						mineral: selectedMineral,
					});
				});
			}
		}
	};

	const EmptyField = () => {
		return (
			<S.EmptyFieldTemp>
				<i className='fal fa-folder-open'></i>
				No results found !
			</S.EmptyFieldTemp>
		);
	};

	return (
		<S.MainField BG={BG00}>
			<S.leftField>
				<div className='header'>
					<img src={Logo} alt='' />
					<div>
						<h1>Mineral Data Miner</h1>
						<p>The final project of bachelor's degree for Amirkabir University of Technology</p>
						<p>Designed and programmed by Rouzbeh Sabzehei</p>
					</div>
				</div>
				<S.Description>
					<h3>Description</h3>
					<p>
						This program is designed to extract mineral data related to countries from the most important and reliable mineral website in
						the world, USGS.
					</p>
					<p>The goal of the project is to simplify the process of obtaining this information, which was previously done manually.</p>
					<small>
						* Note that the information on this website is all from the USGS website and if the information on the mentioned website is
						updated, the information on this website will also be updated. For this reason, the information may not be available for some
						years for some minerals or countries and will be updated in the future.
					</small>
					<p className='links'>
						<i className='fab fa-github'></i>
						Project source code is available on
						<a href='https://github.com/rouzbehsbz/mineral-data-miner' target='_blank' alt=''>
							GitHub
						</a>
					</p>
					<p className='links'>
						<i className='fas fa-download'></i>
						You can also download project thesis from
						<a href='/' target='_blank' alt=''>
							here
						</a>
					</p>
				</S.Description>
				<S.InputField>
					<div className='form_group'>
						<label>Country</label>
						<Autocomplete
							disablePortal
							value={selectedCountry}
							options={countriesList}
							onChange={(_, newValue) => {
								setSelectedCountry(newValue);
							}}
							onInputChange={(_, newValue) => {
								setSelectedCountry(newValue);
							}}
							renderInput={params => <TextField {...params} />}
						/>
					</div>
					<div className='form_group'>
						<label>Mineral</label>
						<Autocomplete
							disablePortal
							value={selectedMineral}
							options={mineralsList}
							getOptionLabel={option => option}
							isOptionEqualToValue={(option, value) => option === value}
							onChange={(_, newValue) => {
								setSelectedMineral(newValue);
							}}
							onInputChange={(_, newValue) => {
								setSelectedMineral(newValue);
							}}
							renderInput={params => <TextField {...params} />}
						/>
					</div>
					<div className='form_group'>
						<label>Topic</label>
						<Autocomplete
							disablePortal
							defaultValue={['Production Rate']}
							options={['Production Rate']}
							getOptionLabel={option => option}
							isOptionEqualToValue={(option, value) => option === value}
							renderInput={params => <TextField {...params} />}
						/>
					</div>
					<Button variant='contained' className='search' onClick={searchHandler}>
						<ButtonLoader title='Search' loading={loader} />
					</Button>
				</S.InputField>
			</S.leftField>
			<S.RightField>
				<div className='header'>
					<h3>Production Rate</h3>
				</div>
				{result?.length !== 0 ? (
					<>
						<div className='chart_field'>
							{selectedinfo.country && <h1>{selectedinfo.country}</h1>}
							<ResponsiveContainer width='100%' height={360}>
								<AreaChart data={result} syncId='anyId'>
									<CartesianGrid strokeDasharray='3 3' />
									<XAxis
										dataKey='year'
										tick={{ fill: '#fff', fontSize: '0.8rem' }}
										label={{ value: 'Year', position: 'insideBottom', offset: -20 }}
									/>
									<YAxis
										tickFormatter={value => value.toLocaleString()}
										tick={{ fill: '#fff', fontSize: '0.8rem' }}
										label={{ value: 'Thousand ton', angle: -90, position: 'insideLeft', offset: -20 }}
									/>
									<Tooltip formatter={(value, name) => [value.toLocaleString(), name === 'country' ? selectedinfo.country : name]} />
									<Area type='monotone' dataKey='country' stroke='#8884d8' fill='#8884d8' />
								</AreaChart>
							</ResponsiveContainer>
						</div>
						<div className='chart_field'>
							{selectedinfo.country && <h1>World</h1>}
							<ResponsiveContainer width='100%' height={360}>
								<AreaChart data={result} syncId='anyId'>
									<CartesianGrid strokeDasharray='3 3' />
									<XAxis
										dataKey='year'
										tick={{ fill: '#fff', fontSize: '0.8rem' }}
										label={{ value: 'Year', position: 'insideBottom', offset: -20 }}
									/>
									<YAxis
										tickFormatter={value => value.toLocaleString()}
										tick={{ fill: '#fff', fontSize: '0.8rem' }}
										label={{ value: 'Thousand ton', angle: -90, position: 'insideLeft', offset: -20 }}
									/>
									<Tooltip formatter={(value, name) => [value.toLocaleString(), name === 'country' ? selectedinfo.country : name]} />
									<Area type='monotone' dataKey='world' stroke='#82ca9d' fill='#82ca9d' />
								</AreaChart>
							</ResponsiveContainer>
						</div>
					</>
				) : (
					<EmptyField />
				)}
			</S.RightField>
		</S.MainField>
	);
}

export default Index;
