import styled from '@emotion/styled';

export const MainField = styled.section(props => ({
	width: '100%',
	minHeight: '100vh',
	background: `url(${props.BG})`,
	backgroundRepeat: 'no-repeat',
	backgroundPosition: 'center',
	backgroundSize: 'cover',
	padding: '50px',
	display: 'flex',
	gap: '30px',

	'@media (max-width: 1200px)': {
		flexDirection: 'column',
	},
}));

export const leftField = styled.div(props => ({
	width: '100%',

	'& .header': {
		display: 'flex',
		alignItems: 'center',
		gap: '40px',
		color: 'white',

		img: {
			width: '120px',
		},

		p: {
			opacity: '0.4',
			fontSize: '0.9rem',
			fontWeight: '300',
		},
	},
}));

export const RightField = styled.div(props => ({
	width: '100%',
	background: 'rgb(255 255 255 / 14%)',
	backdropFilter: 'blur( 5px )',
	borderRadius: '4px',
	padding: '20px 50px 30px 50px',
	color: 'white',

	'@media (max-width: 600px)': {
		padding: '20px 10px 30px 30px',
	},

	'& .chart_field': {
		marginTop: '50px',

		svg: {
			overflow: 'unset !important',
		},

		tspan: {
			fill: 'white',
		},
	},

	h1: {
		width: '100%',
		textAlign: 'center',
	},

	h3: {
		fontSize: '1.5rem',
	},
}));

export const Description = styled.div({
	background: 'rgb(255 255 255 / 14%)',
	backdropFilter: 'blur( 5px )',
	borderRadius: '4px',
	padding: '20px',
	marginTop: '50px',
	color: 'white',

	small: {
		opacity: '0.6',
		fontSize: '0.9rem',
		fontWeight: '300',
		marginTop: '20px',
		display: 'block',
	},

	h3: {
		fontSize: '1.5rem',
	},

	p: {
		marginTop: '20px',
	},

	' & .links': {
		display: 'flex',
		alignItems: 'center',
		gap: '10px',

		a: {
			color: '#1dc2ff',
			textDecoration: 'underline',
		},

		i: {
			fontSize: '1.6rem',
		},
	},
});

export const InputField = styled.div({
	display: 'flex',
	alignItems: 'flex-end',
	gap: '20px',
	justifyContent: 'space-between',
	marginTop: '50px',

	'@media (max-width: 1500px)': {
		flexWrap: 'wrap',
	},

	'@media (max-width: 1200px)': {
		flexWrap: 'unset',
	},

	'@media (max-width: 800px)': {
		flexWrap: 'wrap',
	},

	'& .search': {
		backgroundImage: 'linear-gradient(to right, #314755 0%, #26a0da 51%, #314755 100%)',
		padding: '10px 25px',
		textAlign: 'center',
		textTransform: 'capitalize',
		transition: '.5s',
		backgroundSize: '200% auto',
		color: '#fff',
		borderRadius: '4px',
		display: 'flex',
		gap: '10px',
		minWidth: '120px',
		height: '45.5px',

		'@media (max-width: 610px)': {
			width: '100%',
		},

		'&:hover': {
			backgroundPosition: 'right center',
			color: '#fff',
			textDecoration: 'none',
		},
	},

	fieldset: {
		border: 'none',
	},

	'& .MuiAutocomplete-option.Mui-focused': {
		backgroundColor: '#5d5d5d!important',
	},

	'& [aria-selected="true"]': {
		backgroundColor: '#343434!important',
	},

	'& .MuiInputBase-root': {
		background: 'rgb(255 255 255 / 14%)',
		backdropFilter: 'blur( 5px )',
		borderRadius: '4px',
		color: 'white',
		padding: '5px 50px 5px 5px !important',
		width: '100%',
		fontSize: '0.9rem',
	},

	'& .MuiAutocomplete-root': {
		width: '100%',
	},

	'& .MuiSvgIcon-root': {
		color: 'white',
	},

	'& .MuiPaper-root': {
		background: 'rgb(0 0 0 / 70%)',
		backdropFilter: 'blur( 5px )',
		color: 'white',
		fontSize: '0.9rem',
	},

	'& .form_group': {
		width: '100%',

		'@media (max-width: 800px)': {
			width: '48%',
		},

		'@media (max-width: 610px)': {
			width: '100%',
		},

		label: {
			color: 'white',
			opacity: '0.6',
			fontSize: '0.9rem',
		},
	},
});

export const EmptyFieldTemp = styled.div(props => ({
	width: '100%',
	height: '100%',
	padding: '50px',
	display: 'flex',
	alignItems: 'center',
	justifyContent: 'center',
	color: 'white',
	fontSize: '1.5rem',
	flexDirection: 'column',
	gap: '20px',

	i: {
		fontSize: '4rem',
	},
}));
