import Axios from '../config/axios';

export const GetAllCountries = async () => {
	return Axios.get(`countries`).then(res => res.data.result);
};

export const GetAllMinerals = async () => {
	return Axios.get(`minerals`).then(res => res.data.result);
};

export const GetInformations = async (country, mineral) => {
	return Axios.get(`processes/production-rates?country=${country}&mineral=${mineral}`).then(res => res.data.result);
};
